@font-face {
  font-family: "Oswald Regular";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald Medium";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.advantages-list {
  display: flex;
  flex-direction: column;
  gap: 22px;
}
@media (min-width: 768px) {
  .advantages-list {
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 153px;
  }
}
@media (min-width: 1440px) {
  .advantages-list {
    flex-wrap: nowrap;
    column-gap: 94px;
    padding: 26px 0;
  }
}

.advantages-list__pic {
  min-width: 60px;
  height: 60px;
  background-color: #68b738;
  position: relative;
}
@media (min-width: 768px) {
  .advantages-list__pic {
    max-width: 80px;
    height: 80px;
  }
}

.advantages-list__pic::before {
  content: "";
  background-repeat: no-repeat;
  width: 30px;
  height: 32px;
  display: block;
  position: absolute;
  left: 50%;
  right: 0;
  top: 50%;
  transform: translate(-50%, -50%);
  bottom: 0;
  margin: auto;
}

.advantages-list__pic--leaf::before {
  background-image: url("../img/logos/icon_leaf.svg");
}

.advantages-list__pic--tub::before {
  background-image: url("../img/logos/icon_tub.svg");
}

.advantages-list__pic--eat::before {
  background-image: url("../img/logos/icon_eat.svg");
}

.advantages-list__pic--alarm::before {
  background-image: url("../img/logos/icon_alarm.svg");
}

.advantages-list__item {
  display: flex;
  gap: 20px;
}
@media (min-width: 768px) {
  .advantages-list__item {
    flex-direction: column;
    height: 280px;
    width: 35%;
    position: relative;
  }
}

.advantages-list__desc {
  color: rgb(68, 68, 68);
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  max-width: 100%;
  max-height: max-content;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
@media (min-width: 768px) {
  .advantages-list__desc {
    font-size: 16px;
